@import url(https://cdn.syncfusion.com/ej2/material.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --border: rgb(225, 225, 225);
    --profile: 32px;
    --edit: 52px;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: 0;
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
}
.layout {
    display: flex;
    justify-content: flex-start;
    align-items: start;
}

.sidebar {
    background-color: #323e4d;
    height: 100vh;
    width: 200px;
}

.right_container {
    height: 100vh;
    width: 100%;
    padding: 0;
    margin: 0;
    border: 0;
}

.topbar {
    height: 40px;
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 0px 10px -5px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.topbar button {
    height: 30px;
    border-radius: 5px;
    padding: 0 6px;
    background-color: rgb(35, 35, 255);
    color: white;
    margin-right: 10px;
    cursor: pointer;
}

.content {
    height: calc(100% - 50px);
    width: 100%;
    background-color: #fafafa;
    position: relative;
}

.task_container {
    padding: 50px 0 0 20px;
    width: 320px;
    position: relative;
}

.task_header {
    height: 35px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    border: 1px solid var(--border);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

.task {
    height: 100%;
    background-color: #f9f9fa;
    width: calc(350px - 30px);
    border-right: 1px solid var(--border);
    display: flex;
    text-align: left;
    align-items: center;
    padding-left: 5px;
}

.task_add {
    height: 100%;
    width: 30px;
    background-color: #f9f9fa;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.task_form {
    font-size: 11px;
    padding: 10px 10px 0;
    background-color: #edf7fc;
    border: 1px solid var(--border);
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.task_form label {
    display: block;
    padding : 5px 0;
    font-weight: 600;
}

.task_form input {
    height: 32px;
    border: 1px solid var(--border);
    display: block;
    width: 100%;
    border-radius: 4px;
    padding-left: 4px;
}

.date_time {
    display: grid;
    grid-template-columns: 53% 45%;
    grid-column-gap: 2%;
    -webkit-column-gap: 2%;
            column-gap: 2%;
}

.button_container {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.button_container button{
    height: 23px;
    width: 53px;
    margin-left: 5px;
    border-radius: 3px;
    cursor: pointer;
}

.save {
    background-color: #47bb7f;
    color: white;
    text-align: center;
}

.assign_user select {
    height: 30px;
    width: 100%;
}


.item {
    list-style: none;
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
    position: relative;
    border-bottom: 1px solid var(--border);
    border-left: 1px solid var(--border);
    border-right: 1px solid var(--border);
}

.item:hover .edit {
    visibility: visible;
}

.item_wrap {
    padding: 10px;
    width: 100%;
    height: 100%;
    display: grid;
    align-items: center;
    grid-template-columns: 32px calc(278px - (var(--profile) + var(--edit))) 52px;
    grid-template-rows: -webkit-max-content;
    grid-template-rows: max-content;
    
}

.item_profile {
    width: 32px;
    height: 32px;
}

.item_detail {
    width: 100%;
    padding-left: 5px;
}

.item_editing {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
}

.item_title {
    width: 100%;
    text-align: start;
    line-height: 16px;
    position: relative;
}

.item_title span {
    font-size: 80%;
    font-weight: 600;
}

.item_time {
    width: 100%;
    text-align: start;
    line-height: 16px;
    overflow: hidden;
}

.check, .edit {
    height: 24px;
    width: 24px;
    border: 1px solid black;
    border-radius: 3px;
    cursor: pointer;
}

.edit {
    visibility: hidden;
    margin-right: 3px;
}

.item_time span {
    font-size: 80%;
    display: inline-block;
    margin-right: 5px;
}

.login {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: lightgreen;
}

.login_form {
    border-radius: 10px;
    width: 300px;
    padding: 10px;
    background-color: lightslategray;
}

.login_form label {
    display: block;
    padding: 5px 0;
}

.login_form input {
    display: block;
    height: 32px;
    border-radius: 5px;
    width: 90%;
    padding: 5px;
}

.login_form_control {
    padding-bottom: 18px;
    position: relative;
}

.login_form_control p {
    font-size: 80%;
    color: red;
    bottom: 0;
    padding: 3px 0 0 3px;
}

.login_button {
    margin: 8px 0;
}

.login_button button {
    height: 30px;
    border-radius: 5px;
    padding: 0 6px;
    background-color: rgb(35, 35, 255);
    color: white;
    cursor: pointer;
}

.top_error_message {
    color: red;
    font-size: 85%;
}

